import React, { Component } from "react";
import { HashRouter, Route, Switch } from "react-router-dom";

// import AppNavBar from "./component/layout/AppNavBar";

import BioDiv from "./component/layout/BioDiv";
import Portfolio from "./component/layout/Portfolio";
import Homepage from "./component/layout/Homepage";

import "./App.scss";
import Background from "../src/asset/background_image_malaysia_bnw.jpg";

class App extends Component {
  render() {
    const backgroundStyle = {
      backgroundImage: `url(${Background})`,
      backgroundSize: "cover",
      backgroundPosition: "center",
      backgroundRepeat: "no-repeat",
      zIndex: "-1"
    };
    return (
      <HashRouter basename="/">
        <div className="App" style={backgroundStyle}>
          <BioDiv />
          <div className="site-content">
            <Switch>
              <Route exact path="/" component={Homepage} />
              <Route exact path="/portfolio" component={Portfolio} />
            </Switch>
          </div>
        </div>
      </HashRouter>
    );
  }
}

export default App;
