import React, { Component } from "react";

import canaan1 from "../../asset/canaan/canaan_homepage.png";
import canaan2 from "../../asset/canaan/post_a_job.png";
import canaan3 from "../../asset/canaan/sign_up_function.png";

import contactManager1 from "../../asset/contactManager/contactManager1.png";
import contactManager2 from "../../asset/contactManager/contactManager2.png";
import contactManager3 from "../../asset/contactManager/contactManager3.png";

import clientPanel1 from "../../asset/clientPanel/clientPanel1.png";
import clientPanel2 from "../../asset/clientPanel/clientPanel2.png";
import clientPanel3 from "../../asset/clientPanel/clientPanel3.png";

class PortfolioItems extends Component {
  state = {
    portfolio_items: [
      {
        "project-title": "CanaanJobs",
        "project-img": [canaan1, canaan2, canaan3],
        "project-link": "https://khsyo.github.io/connect-the-x/#/",
        "project-github": "https://github.com/khsyo/connect-the-x",
        "project-caption": ["caption1", "caption2", "caption3"],
        date: "2019 May"
      },
      {
        "project-title": "Contact Manager",
        "project-img": [contactManager1, contactManager2, contactManager3],
        "project-link": "https://khsyo.github.io/contactmanager/#/",
        "project-github": "https://github.com/khsyo/contactmanager",
        "project-caption": ["caption1", "caption2", "caption3"],
        date: "2019 Feb"
      },
      {
        "project-title": "Client Panel",
        "project-img": [clientPanel1, clientPanel2, clientPanel3],
        "project-link": "https://khsyo.github.io/reactclientpanel/#/",
        "project-github": "https://github.com/khsyo/reactclientpanel",
        "project-caption": ["caption1", "caption2", "caption3"],
        date: "2018 Dec"
      }
    ]
  };
  render() {
    const portfolio_items = this.state.portfolio_items
      ? this.state.portfolio_items.map((project, index) => (
          <div className="portfolio-items--panel">
            <div className="portfolio-items--panel__date">
              <span className="portfolio-items--panel__date-child">
                {project.date}
              </span>
            </div>
            <div className="portfolio-items--panel__main">
              <h4>{project["project-title"]}</h4>
              <div className="portfolio-items--panel__description">xxxx</div>
              <div className="portfolio-items--panel__stack">xxx</div>
            </div>
            <div className="portfolio-items--panel__img">
              <img src={project["project-img"][0]} />
            </div>
          </div>
        ))
      : null;
    return <div className="portfolio-items">{portfolio_items}</div>;
  }
}

export default PortfolioItems;
